.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 60px;
    box-shadow: 0 1px 3px -2px #000;
    margin-bottom: 1px;
    background: white;
}

.nav-logo{
    width: 200px;

}

.nav-profile{
    width: 75px;
}


@media screen and (max-width: 600px) {
    .navbar {
        padding: 15px; 
    }

    .nav-logo {
        width: 120px; 
    }

    .nav-profile {
        width: 40px; 
    }
}